<script>
    import "./../assets/css/commong.css"
    import hearder from "@/components/hearder"
    import footder from "@/components/footder"
    import crumbs from "@/components/crumbs"
    import Modal from "@/components/Modal"
    import axios from "axios"
    export default {
        name: '',
        data () {
        return {
            goodslist:[],
            sortFlage:true,
            page:1,
            pageSize:5,
            busy:true,
            priceChecked:"all",
            mdShow:false,
            mdShow2:false,
            region:[
              {
                "place":"宝安"
              },
              {
                "place":"福田"
              },
              {
                "place":"罗湖"
              }
            ]
            }
        },
        components:{
            'hearder':hearder,
            "footder":footder,
            "crumbs":crumbs,
            "Modal":Modal
        },
        mounted:function(){
          this.getGoodslist();
        },
        methods:{
          getGoodslist(flag){
            var param={
              page:this.page,
              pageSize:this.pageSize,
              sort:this.sortFlage?1:-1,
              priceLevel:this.priceChecked
            }
            axios.get("/goods/List",{
              params:param
            }).then((result)=>{
              var res=result.data;
              if(res.status=="0"){
                if(flag){
                  this.goodslist=this.goodslist.concat(res.result.list);
                  if(res.result.count==0){
                    this.busy=true;
                  }else{
                    this.busy=false;
                  }
                }else{
                  this.goodslist=res.result.list;
                  this.busy=false;
                }

              }else{
                this.goodslist={};
              }
            })
          },
          sortGoods(){
             this.sortFlage=!this.sortFlage;
             this.page=1;
             this.getGoodslist();
          },
          setPriceFilter(index){
             this.priceChecked=index;
             this.page=1;
             this.getGoodslist()
          },
          loadMore(){
            this.busy=true;
            setTimeout(() => {
              this.page++;
              this.getGoodslist(true)
            }, 500);
          },
          addCart(postId){
            axios.post("/goods/addCart",{
              postId:postId
            }).then((response)=>{
              let res=response.data;
              if(res.status==0){
                this.mdShow2=true;
              }else{
                this.mdShow=true;
              }
            })
          },
          closeModal(){
            this.mdShow=false;
          }
        }

    }
</script>

