<template>
    <div class="header">
        <div class="w clearfix">
            <a class="jz-logo" href="/" title="千牛兼职">千牛兼职</a>
            <a class="location" href="/cityselect/">深圳</a>
            <ul class="nav" data="">
                <li>
                    <a href="/">首页</a>
                </li>
                <li>
                    <a href="/sz/" class="zhiweiCity">职位</a>
                </li>

                <li>
                    <a href="/tegong/">斗米特工</a>
                </li>
                <li>
                    <a href="http://vip.doumi.com" onclick="_hmt.push(['_trackEvent', 'pc_home_shanjiafatie', 'click'])" target="_blank" rel="nofollow">企业版</a>
                </li>
            </ul>
            <div class="login-info">
                <span v-text="nickName" v-if="nickName"></span>
                <a href="" href="javascript:;" @click="logOut" v-if="nickName">退出</a>
                <a class="login-reg jz_lg" v-if="!nickName" href="javascript:;" @click="longinModalFlag=true">注册登录</a>
            </div>
        </div>
        <!-- 登录弹窗-->
        <!-- 黑布-->
      <div class="js-modal-mask "  @click="longinModalFlag=false" v-bind:class="{'js_show':longinModalFlag}" style="height: 6546px;"></div>
        <!--登录模块 -->
      <div class="box" href="javascript:;" v-bind:class="{'js_show':longinModalFlag}">
        <h2>登录</h2>
        <a class="ganbi" @click="longinModalFlag=false">X</a>
        <div id="error_box" v-show="errorTip">用户名或者密码错误</div>
        <div class="input_box">
          <input type="text" v-model="userName" placeholder="请输入账户名" id="uname"/>
        </div>
        <div class="input_box">
          <input type="password" v-model="userPwd" placeholder="请输入密码" id="upass"/>
        </div>
        <div class="input_box">
          <button  @click="login">登录</button>
        </div>
      </div>
      <!-- -->
    </div>
</template>

<script type="text/ecmascript-6">
    import axios from "axios"
    export default {
        name: '',
        data () {
        return {
            userName:"",
            userPwd:"",
            errorTip:"",
            longinModalFlag:false,
            nickName:""
         }
        },
        mounted(){
          this.checkLogin();
        },
        methods:{
          checkLogin(){
             axios.get("/users/checkLogin").then((response)=>{
                let res=response.data;
                if(res.status=="0"){
                  this.nickName=res.result;
                }
             })
          },
           login(){
            if(!this.userName || !this.userPwd){
              this.errorTip=true;
              return;
            }
              axios.post("/users/login",{
                userName:this.userName,
                userPwd:this.userPwd
              }).then((response)=>{
                 let res=response.data;
                 if(res.status=="0"){
                   this.errorTip=false;
                   this.longinModalFlag=false;
                   this.nickName=res.result.userName;
                 }else{
                   this.errorTip=true;

                 }
            })
          },
          logOut(){
            axios.post("/users/logout").then((response)=>{
               let res=response.data;
               if(res.status=="0"){
                 this.nickName="";
               }
            })
          }

        }

    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
  .ganbi{
    display: block;
    float: right;
    margin-top: -40px;
    margin-right: 10px;
    font-size: 30px;
    color: #3333;

  }
  .ganbi:before{
    clear: both;
  }
  .js-modal-mask {
    position: fixed;
    background-color: #000;
    bottom: 0px;
    font-size: 14px;
    height: 100%;
    left: 0px;
    opacity: 0.2;
    filter: alpha(opacity=20);
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(opacity=20)";
    right: 0px;
    top: 0px;
    z-index: 11030;
    display: none;
  }

   .js-modal-mask {

    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
  }
  .box{
    position: fixed;
    z-index: 11031;
    width:390px;
    height:320px;
    border:solid 1px #ddd;
    background:#FFF;
    left:50%;
    top:42%;
    margin-left:-195px;
    margin-top:-130px;
    display: none;
  }
  .js_show {
    display: block;
  }
  .box h2{
    font-weight:normal;
    color:#666;
    font-size:16px;
    line-height:46px;
    height:46px;
    overflow:hidden;
    text-align:center;
    border-bottom:solid 1px #ddd;
    background:#f7f7f7;
  }
  .input_box{
    width:350px;
    padding-bottom:15px;
    margin:0 auto;
    overflow:hidden;
  }
  .input_box input{
    float:left;
    width:348px;
    height:40px;
    font-size:14px;
    border:solid 1px #ddd;
    text-indent:10px;
    outline:none;
    line-height:40px;
  }
  .input_box button{
    width:350px;
    height:48px;
    background:#3f89ec;
    border:none;
    border-radius:2px;
    outline:none;
    cursor:pointer;
    font-size:16px;
    color:#FFF;
  }
  #error_box{
    height:40px;
    width:350px;
    margin:0 auto;
    line-height:40px;
    color:#fc4343;
  }
</style>



