
<template>
<div>
  <!-- 弹窗模块-->
  <!-- 黑布-->
  <div class="js-modal-mask "  @click="closeModal" v-bind:class="{'js_show':mdShow}" style="height: 6546px;"></div>
  <!--弹窗模块 -->
  <div class="Modal" >
    <div class="box" v-bind:class="{'js_show':mdShow}">
      <h2></h2>
      <a class="ganbi" @click="closeModal">X</a>

      <div class="confirm-tips">
        <slot name="message"> </slot>
      </div>

      <div class="btn-wrap">
        <slot name="btnGroup"></slot>
      </div>
    </div>
  </div>
  <!-- -->
</div>
</template>

<script>
  export default {
    props:["mdShow"],
    name: 'hello',
    data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
      methods:{
      closeModal(){
         this.$emit("close");
         trigger("click")
      }
    }

  }
</script>

